*{margin:0;padding:0;box-sizing:border-box;text-transform:capitalize;}
body{background:black;}
a{text-decoration:none;}
ul{list-style:none;}
/* navbar */
.header{display:flex;justify-content:space-between;align-items:center;
  padding:20px;position:fixed;width:100%;height:90px;z-index:10;}
  .header-bg{background:rgba(0,0,0,0.85);transition:0.5s;}
.header h1{color:yellow;font-size:40px;font-weight:700;
letter-spacing:1px;}
.nav-menu{display:flex;}
.nav-menu li{padding:0 20px;font-size:23px;font-weight:600;}
.nav-menu a{color:white;transition:0.5s;}
.nav-menu a:hover{color:darkturquoise;}
.hamburger{position:absolute;top:40px;right:20px;display:none;}
@media  (max-width:690px){
  .header{display:block;}
  .nav-menu{flex-direction:column !important;justify-content:center;
  align-items:center;background:rgba(255, 255, 255, 0.053);width:100%;
  padding:20px 0;margin:20px 0;position:absolute;left:-100%;
transition:0.6s;z-index:-3;}
.nav-menu.active{left:0;justify-content:center;}
.nav-menu li{margin:15px 0;}
.hamburger{display:block;}
}
@media (max-width:800px) {
  .footer .flex{flex-direction:column;align-items:center;}
  .footer .fl{margin:15px 0;}
  .pricing .card-container{margin:50px 0;flex-direction:column;
  align-items:center;}
  .pricing .card-container .card{width:50%;margin:13px 0;}
  .card .btn{font-size:18px;padding:10px;width:80%;}
  
}

/* hero */
.hero{overflow:hidden;color:white;}
.mask{width:100vw;height:100vh;position:relative;}
.mask img{width:100%;height:100%;}
.mask::after{content:'';width:100%;height:100%;position:absolute;top:0;
left:0;background:#000;opacity:0.7;}
.hero .content{position:absolute;transform:translate(-50%,-50%);top:50%;
left:50%;text-align:center;}
.hero .content h1{font-size:40px;letter-spacing:1px;margin:10px 0;}
.hero .content p{font-weight:600;letter-spacing:2px;}
.btn{border:2px solid white;padding:10px 20px;display:inline-block;
margin:10px;font-size:20px;font-weight:600;color:white;
text-transform:uppercase;transition:0.6s;}
.btn:hover{background:yellow;color:black;border:2px solid yellow;}
.btn-light{border:2px solid yellow;}
.btn-light:hover{background:yellow;color:black;}


/* projects */
.heroimgpro{color:white;width:100%;height:70vh;position:relative;}
.heroimgpro::before{content:'';width:100%;
background:linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6),
rgba(0,0,0,0.6)),url('./Components/images/programming1.jpg');
background-size:100% 100%;position:absolute;top:0;left:0;height:100%;
z-index:-1;}
.heading{width:100%;height:100%;display:flex;flex-direction:column;
justify-content:center;align-items:center;}
.heading h1{letter-spacing:2px;text-transform:uppercase;}
.heading p{margin:10px 0;color:darkturquoise;font-size:18px;}

/* pricing */
.pricing{color:white}
.pricing .card-container{margin:50px 0;display:flex;
justify-content:space-around;}
.card{border:2px solid white;width:25%;text-align:center;transition:0.6s;
padding:20px 0;}
.card:hover{transform:translateY(5px);border:2px solid darkturquoise;}
.card h3{opacity:0.7;margin:10px 0;}
.card h2{color:darkorange;margin:15px 0;font-weight:700;}
.card ul li{margin:12px 0;font-size:18px;font-weight:600;}

/* projectcard */
.work-container{color:white;text-align:center;margin-top:40px;}
.project-card img{width:70%;margin-bottom:20px;height:400px;}
.project-heading{margin:15px 0;letter-spacing:2px;}
.project-title{margin:10px 0;font-size:25px;letter-spacing:1px;
color:darkorange;}
.project-text{font-style:18px;font-weight:600;}
.pro-btns{margin:20px;display:flex;justify-content:space-evenly;}
.project-card{background:#3333336f;width:70%;margin:25px auto;
padding:30px 0;}

/* about */
.about{color:white;text-align:center;margin:50px 0;}
.about .left h1{color:darkorange;margin:10px 0;}
.about .left p{font-size:18px;margin-bottom:20px;}
.about .img-container{display:grid;position:relative;
grid-template-columns:repeat(12,1fr);align-items:center;text-align:center;}
.about .right .img{width:50%;height:400px;border:2px solid white;}
.right .top{grid-column:1 / span 8;grid-row:1;padding-top:20%;z-index:6;}
.right .bottom{grid-column:4 / -1;grid-row:1;z-index:5;}

/* footer */
.footer{margin-top:90px;color:white;}
.footer .flex{display:flex;justify-content:space-around;}
.footer .flex .fl{display:flex;align-items:center;}
.footer .flex h3{margin:0 8px;color:yellow;}
.textfooter{text-align:center;margin-top:60px;margin-bottom:40px;}
.textfooter p{margin:10px 0;}
.textfooter .social{margin-top:30px;}
.textfooter .social .icon{margin:0 15px;}